#miniShop {
    display: flex;
    flex-basis: 100%;
    flex-wrap: wrap;
    max-width: 100vw;
    overflow: hidden;
    max-width: 1200px;
    margin: 0 auto;

    h2 {
        display: flex;
        flex-basis: 100%;
        margin: 0;
        font-weight: 500;
        font-size: 1.2em;
        padding: 0 20px;
        position: relative;
        top: 30px;
    }
    .articlesContainer {
        display: flex;
        column-gap: 30px;
        flex-basis: 100%;
        padding: 60px 40px;
        overflow-x: scroll;

    }
}