.atouts {
    display: flex;
    background-color: black;
    flex-basis: 100%;
    > div {
        display: flex;
        flex-wrap: wrap;
        flex-basis: 100%;
        max-width: 1200px;
        margin: 0 auto; 
        justify-content: space-around;
        padding: 60px 0 40px 0;
        align-items: center;
        > div {
            flex-wrap: wrap;
            display: flex;
            justify-content: center;
            align-items: center;
            height: 100px;
            > svg {
                font-size: 1.5em;
                color: rgba(241, 186, 83, 0.84);

            }
            > p {
                display: flex;
                flex-basis: 100%;
                justify-content: center;
                color: white;
                font-size: 0.8em;
                text-align: center;
            }
        }
    }
}
@media screen and (min-width: 1450px) {
    .atouts {
        > div {
            padding: 40px 0 50px 0;
            > div {
                
                > svg {
                    font-size: 2.5em;
                }
                > p {
                    font-size: 1em;
                }
            }
        }
    }
}