.articleCard {
    display: flex;
    flex-basis: 25%;
    min-width: 250px;
    flex-wrap: wrap;
    position: relative;
    border-radius: 10px;
    box-shadow:0px 0px 41px 1px rgba(142, 140, 140, 0.461);
    animation: slide-top 0.7s ease-in-out ;
    figure {
        display: flex;
        flex-basis: 100%;
        height: 200px;
        margin: 0;
        > img {
            position: relative;
            display: flex;
            object-fit: cover;
            width: 100%;
            height: 200px;
            border-top-left-radius: 10px;
            border-top-right-radius: 10px;
        }
    }
    > div {
        display: flex;
        flex-wrap: wrap;
        padding: 20px;
        h3 {
            margin: 10px auto;
            display: flex;
            flex-basis: 100%;
            font-weight: 400;
            font-size: 0.8em;
            justify-content: center;
            color:  rgb(0, 0, 0);
        }
        > p {
            position: absolute;
            display: flex;
            top: 10px;
            left: 20px;
            font-size: 0.8em;
            align-items: center;
            font-weight: 500;
            color: white;
            > span {
                height: 10px;
                width: 10px;
                background-color: green;
                border-radius: 50%;
                margin-left: 10px;
            }
        }
        > button {
            margin: 20px auto;
            padding: 10px 20px;
            background-color: rgb(17, 17, 17);
            border-radius: 5px;
            border: none;
            font-weight: 600;
            color: white;
            font-size: 0.8em;
        }
    }
}