.story {
    max-width: 1200px;
    display: flex;
    flex-basis: 100%;
    flex-wrap: wrap;
    margin: 0 auto;
    padding: 20px;
    > figure {
        margin: 0;
        flex-basis: 100%;
        position: relative;
        > img {
            height: 300px;
            object-fit: cover;
            width: 100%;
        }
        > div {
            padding: 30px;
            width: 70%;
            position: absolute;
            top: 40px;
            left: 40px;
            font-weight: 600;
            color: white;
            background-color: rgba(0, 0, 0, 0.822);
            h2 {
                color: rgb(167, 120, 32);
            }
            p {
                font-weight: 200;
                font-size: 0.8em;
            }
        }
    }
    
}
@media screen and (min-width: 1450px) {
    .story {
        margin-top: 40px;
        > figure {
            > img {
                height: 600px;
            }
        }
    }
}