.homepage {
    display: flex;
    flex-wrap: wrap;
    flex-basis: 100%;
     > figure{
        display: flex;
        flex-basis: 100%;
        height: 300px;
        position: relative;
        margin: 0;
        align-items: center;
        justify-content: center;
        overflow: hidden;
        margin-bottom: -30px;
         video {
            position: absolute;
            top: -30px;
            left: 0;
            margin: 0;
            padding: 0;
            object-fit: cover;
            width: 100%;
            height: 490px;

        }
        > img {
            z-index: 3;
            height: 100px;
            position: relative;
            animation: slide-bottom 0.8s ease-in-out;
        }

    }
}
@media screen and (min-width: 1450px) {
    .homepage {
         > figure {
            height: 700px;
            video {
                height: 700px;
            }
            > img {
                height: 200px;
            }
        }
    }
}