@import '../../animations/headerAnimations.scss';

header {
    position: fixed;
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: flex-start;
    background-color: white;
    box-shadow: 0px -4px 38px 2px rgba(0, 0, 0, 0.137);
    z-index: 999;
    background-color: white;
     > a {
        display: flex;
        height: 90px;
        padding: 20px 30px;
        background-color: black;
        > img {
            height: 70px;
        }
    }
    > .button_nav {
        background-color: white;
        border: none;
        position: fixed;
        right: 30px;
        top: 60px;
        height: 2px;
        width: 30px;
        background-color: black;
        cursor: pointer;
        > span {
            color: transparent;
            position: relative;
            bottom: 20px;
            font-size: 2em;
            min-height: 30px;
            min-width: 50px;
        }
        &::after {
            content: ' ';
            position: absolute;
            height: 2px;
            width: 30px;
            background-color: black;
            position: absolute;
            top: -10px;
            left: 0;
           
        }
        &::before {
            content: ' ';
            position: absolute;
            height: 2px;
            width: 30px;
            background-color: black;
            position: absolute;
            top: 10px;
            left: 0;

        }
    }
    .button_nav-open {
        background-color: white;
        border: none;
        position: fixed;
        right: 30px;
        top: 60px;
        height: 2px;
        width: 30px;
        background-color: white;
        cursor: pointer;
        > span {
            color: transparent;
            position: relative;
            bottom: 20px;
            font-size: 2em;
            min-height: 30px;
            min-width: 50px;
        }
        &::after {
            content: ' ';
            position: absolute;
            height: 2px;
            width: 30px;
            background-color: black;
            position: absolute;
            top: 0;
            left: 0;
            transform: rotate(45deg);
            transition: transform 0.5s ease-in-out ;
            
           
        }
        &::before {
            content: ' ';
            position: absolute;
            height: 2px;
            width: 30px;
            background-color: black;
            position: absolute;
            top: 0;
            left: 0;
            transform: rotate(-45deg);
            transition: transform 0.5s ease-in-out ;
        }
    }
    > nav {
        display: flex;
        flex-wrap: wrap;
        position: relative;
        height: 90vh;
        flex-basis: 100%;
        animation: display-nav 0.3s ease-in-out 0s ;
        .navContainer {
            display: flex;
            flex-basis: 100%;
            flex-wrap: wrap;
            height: 100px;
            margin-top: 170px;
            row-gap: 30px;

            > a {
                display: flex;
                text-decoration: none;
                justify-content: center;
                color: black; 
                font-size: 1.3em;
                font-family: 'Montserrat', sans-serif;
                font-weight: 200;
                flex-basis: 100%;
                &:first-child {
                    animation: slide-top 0.4s ease-in-out 0.4s both;
                }
                &:nth-child(2) {
                    animation: slide-top 0.4s ease-in-out 0.6s both;
                }
                &:nth-child(3) {
                    animation: slide-top 0.4s ease-in-out 0.8s both;
                }
                &:nth-child(4) {
                    padding: 10px 20px;
                    border-radius: 10px;
                    animation: slide-top 0.4s ease-in-out 1s both;
                }
            }
            
        }
    }
    
}
@media screen and (min-width: 1450px) {
    header {
        > a {
            height: 160px;
            width: 47%;
            display: flex;
            justify-content: center;
            align-items: center;
            > img {
                height: 110px;
            }
        }
        > .button_nav  {
            top: 100px;
        }
        .button_nav-open {
            top: 100px;
        }
    }
}